@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
    --color1: #E4FBFF;
    --color2: #EDEEF7;
    --color3: #B8B5FF;
    --color4: #7868E6;
    --color5: #161D6F; 
}

.even-section {
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: #EDEEF7;
    background-color: var(--color2);
}

.navbar {
    background-color: #161D6F;
    background-color: var(--color5);
}

.homepage .jumbotron {
    background-color: #E4FBFF;
    background-color: var(--color1);
    margin-bottom: 0px;
    height: 95vh;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.homepage .jumbotron h1 {
    font-size: clamp(3rem,5vw,5rem);
    color: #161D6F;
    color: var(--color5);
    margin-bottom: 50px;
}

.homepage .jumbotron button {
    font-size: 20px;
    color: #E4FBFF;
    color: var(--color1);
    background-color: #7868E6;
    background-color: var(--color4);
}

.popular-resources h1 {
    color: #161D6F;
    color: var(--color5);
}

.popular-resources .card {
    box-shadow: 0 2px 4px 0 rgba(22, 29, 11, 0.2), 0 5px 15px 0 rgba(22, 29, 11, 0.19);
    border: 0px;
}

.popular-resources .card .card-header{
    background-color: #161D6F;
    background-color: var(--color5);
    color: #EDEEF7;
    color: var(--color2);
}

.popular-resources .card .card-body{
    background-color: #E4FBFF;
    background-color: var(--color1);
    color: #161D6F;
    color: var(--color5);
}

.popular-resources .card .card-body .user{
    color: #7868E6;
    color: var(--color4);
}

.popular-resources .card .button {
    background-color: #7868E6;
    background-color: var(--color4);
    color: #EDEEF7;
    color: var(--color2);
}
.title {
  margin: 0;
  margin-right: 1rem;
}

.searchFlex {
  flex: 5 1;
}

.nav_blogs {
  /* color: white;
    background-color:#75cfb8;
    display: flex;
    position: fixed;
    
    width: 100vw;
    z-index: 1;
    flex-direction: row;
    justify-content: space-between;
